.firstPage {
    height: 100vh;
    width: 100vw;
    padding-top: 10vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-image: url(/img/first_page/tomato-2780424_1920_2.jpg);
    background-size: cover;
    font-family: "playball", sans-serif;
    h1 {
        margin: 10vw;
        font-size: 21vw;
        text-align: center;
        color: #fffdfe;
    }
    a {
        margin: 5vh 10vw;
        padding: 3vw;
        background-color: #f31c42;
        font-size: 8vw;
        text-decoration: none;
        text-align: center;
        color: #fff;
        border-radius: 25%;
    }
}